<script setup>
import { ref } from 'vue'
import axios from 'axios'

const currentSlide = ref(0)
let field_gender = ref('')
let field_age = ref('')
let field_condition = ref('')
let form_response = ref(null)
let products_category = ref('')
let products_message = ref('')
let products_response = ref(null)
let currentMessage = ref('')
let messages = []

async function submitMessage() {
  const response = await axios.post(
    'https://chatbot-mvp-ptvum.ondigitalocean.app/api/openai',
    {
      message: currentMessage.value,
    }
  )

  messages.push({ message: currentMessage.value, from: 'user' })
  messages.push({ message: response.data, from: 'ai' })
  currentMessage.value = ''
}

async function submitForm() {
  const response = await axios.post(
    'https://chatbot-mvp-ptvum.ondigitalocean.app/api/arfinn',
    {
      gender: field_gender.value,
      age: field_age.value,
      condition: field_condition.value,
    }
  )
  form_response.value = response.data
}

async function submitProducts() {
  const response = await axios.post(
    'https://chatbot-mvp-ptvum.ondigitalocean.app/api/products',
    {
      category: products_category.value,
      message: products_message.value,
    }
  )
  products_response.value = response.data
}

function resetForm() {
  form_response.value = null
  field_gender.value = ''
  field_age.value = ''
  field_condition.value = ''
  field_message.value = ''
}

function resetProducts() {
  products_response.value = null
  products_category.value = ''
  products_message.value = ''
}
</script>

<template>
  <div id="window">
    <div id="top-main"
         v-if="currentSlide === 0">
      <div class="main-head">
        <h1>Hi, I'm Arfinn!</h1>
        <svg @click="$emit('toggleWindow')"
             xmlns="http://www.w3.org/2000/svg"
             fill="none"
             viewBox="0 0 24 24"
             stroke-width="2"
             stroke="white"
             class="w-6 h-6">
          <path stroke-linecap="round"
                stroke-linejoin="round"
                d="M6 18L18 6M6 6l12 12" />
        </svg>
      </div>
      <p class="subtitle">I'm a specialized AI bot that can help answer any of your questions about Arfinnmed.</p>
    </div>
    <div id="top-main"
         v-if="currentSlide === 1">
      <div class="flex flex-row items-center space-x-4">
        <div class="rounded-md w-6 h-6 bg-transparent hover:bg-arfinnPurple-800 flex items-center justify-center cursor-pointer"
             @click="currentSlide = 0">
          <svg xmlns="http://www.w3.org/2000/svg"
               fill="none"
               viewBox="0 0 24 24"
               stroke-width="2"
               stroke="white"
               class="w-4 h-4">
            <path stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M15.75 19.5L8.25 12l7.5-7.5" />
          </svg>
        </div>
        <h2>No problem!</h2>
      </div>
      <p class="subtitle">
        Tell us a little bit more about yourself and your condition and we'll scour thousands of pages of clinical research to see what has worked best for others like you.
      </p>
    </div>
    <div id="top-main"
         v-if="currentSlide === 2">
      <div class="flex flex-row items-center space-x-4">
        <div class="rounded-md w-6 h-6 bg-transparent hover:bg-arfinnPurple-800 flex items-center justify-center cursor-pointer"
             @click="currentSlide = 0">
          <svg xmlns="http://www.w3.org/2000/svg"
               fill="none"
               viewBox="0 0 24 24"
               stroke-width="2"
               stroke="white"
               class="w-4 h-4">
            <path stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M15.75 19.5L8.25 12l7.5-7.5" />
          </svg>
        </div>
        <h2>Browse products</h2>
      </div>
      <p class="subtitle">
        Quickly search and browse available products in stock in dispensaries near you.
      </p>
    </div>
    <div id="top-main"
         v-if="currentSlide === 3">
      <div class="flex flex-row items-center space-x-4">
        <div class="rounded-md w-6 h-6 bg-transparent hover:bg-arfinnPurple-800 flex items-center justify-center cursor-pointer"
             @click="currentSlide = 0">
          <svg xmlns="http://www.w3.org/2000/svg"
               fill="none"
               viewBox="0 0 24 24"
               stroke-width="2"
               stroke="white"
               class="w-4 h-4">
            <path stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M15.75 19.5L8.25 12l7.5-7.5" />
          </svg>
        </div>
        <h2>Chat with us</h2>
      </div>
      <p class="subtitle">
        Simply type in your question below to get an immediate answer!
      </p>
    </div>

    <div class="p-4 flex flex-col space-y-2"
         v-if="currentSlide === 0">
      <div class="big-button"
           @click="currentSlide = 1">
        <p class="button-title">Need relief from a medical condition?</p>
        <p class="button-subtitle">I can find a product that works best for you!</p>
      </div>
      <div class="big-button"
           @click="currentSlide = 2">
        <p class="button-title">Browse available products...</p>
        <p class="button-subtitle">Quickly find available products in stock!</p>
      </div>
      <div class="big-button"
           @click="currentSlide = 3">
        <p class="button-title">Ask me anything!</p>
        <p class="button-subtitle">Go ahead, ask me anything!</p>
      </div>
    </div>

    <div class="p-4 flex flex-col flex-1 space-y-2"
         v-if="currentSlide === 1 && form_response === null">
      <select placeholder="Gender"
              class="text-input"
              v-model="field_gender">
        <option value=""
                disabled
                selected>Gender</option>
        <option value="male">Male</option>
        <option value="female">Female</option>
        <option value="other">Other</option>
      </select>
      <select placeholder="Age Range"
              class="text-input"
              v-model="field_age">
        <option value=""
                disabled
                selected>Age Range</option>
        <option value="18-25">18-25</option>
        <option value="26-35">26-35</option>
        <option value="36-45">36-45</option>
        <option value="46+">46+</option>
      </select>
      <select placeholder="Condition"
              class="text-input"
              v-model="field_condition">
        <option value=""
                disabled
                selected>Condition</option>
        <option value="alpha">Alpha</option>
        <option value="bravo">Bravo</option>
        <option value="charlie">Charlie</option>
      </select>
      <div class="flex-1"></div>
      <button class="primary-btn"
              @click="submitForm">Submit</button>
    </div>
    <div class="p-4 flex flex-col flex-1 space-y-2"
         v-if="currentSlide === 1 && form_response !== null">
      <p class="text-sm text-slate-700">{{ form_response }}</p>
      <div class="flex-1"></div>
      <button class="primary-btn"
              @click="resetForm">Start Over</button>
    </div>

    <div class="p-4 flex flex-col flex-1 space-y-2"
         v-if="currentSlide === 2 && products_response === null">
      <select placeholder="Category"
              class="text-input"
              v-model="products_category">
        <option value=""
                disabled
                selected>Category</option>
        <option value="alpha">Alpha</option>
        <option value="bravo">Bravo</option>
        <option value="charlie">Charlie</option>
      </select>
      <textarea placeholder="Your message"
                class="text-input flex-1"
                v-model="products_message" />
      <button class="primary-btn"
              @click="submitProducts">Submit</button>
    </div>
    <div class="p-4 flex flex-col flex-1 space-y-2"
         v-if="currentSlide === 2 && products_response !== null">
      <p class="text-sm text-slate-700">{{ products_response }}</p>
      <div class="flex-1"></div>
      <button class="primary-btn"
              @click="resetProducts">Start Over</button>
    </div>

    <div class="p-4 flex flex-col flex-1 space-y-2 overflow-y-scroll"
         v-if="currentSlide === 3">
      <div v-for="message in messages"
           :key="message"
           :class="['message', message.from]">
        <p class="message-text">{{ message.message }}</p>
      </div>
    </div>

    <div class="flex-1"
         v-if="currentSlide === 0"></div>
    <input type="text"
           placeholder="Ask me anything!"
           class="chat-input"
           v-model="currentMessage"
           @input="currentSlide = 3"
           @keypress.enter="submitMessage"
           v-if="currentSlide === 0 || currentSlide === 3" />
  </div>
</template>

<style>
  #window {
		@apply w-screen md:w-full md:max-w-[24rem] h-full lg:max-h-[700px] bg-slate-50 rounded-none md:rounded-md fixed top-0 md:top-auto bottom-0 md:bottom-28 right-0 md:right-6 flex flex-col ml-0 md:ml-6;

		box-shadow: 0 0.125em 0.125em 0 rgb(0 0 0 / 14%), 0 0.0625em 0.3125em 0 rgb(0 0 0 / 12%), 0 0.1875em 0.0625em -0.125em rgb(0 0 0 / 20%);
  }

	#top-main {
		@apply bg-arfinnPurple-900 p-8 rounded-t-none md:rounded-t-md;
	}

  .main-head {
    @apply flex justify-between items-center;
  }

	h1 {
		@apply text-white font-bold text-3xl !important;
	}

	h2 {
		@apply text-white font-bold text-lg !important;
	}

	.subtitle {
		@apply text-slate-200 text-sm mt-4;
	}

	.big-button {
		@apply w-full px-4 py-3 border border-slate-200 rounded-md hover:bg-blue-50 bg-white cursor-pointer;
	}

	.button-title {
		@apply text-arfinnPurple-900 font-semibold text-sm;
	}

	.button-subtitle {
		@apply text-slate-700 text-sm;
	}

	.chat-input {
		@apply px-4 py-3 rounded-b-md border-b-0 border-l-0 border-r-0 border-t border-slate-200 focus:border-slate-200 focus:outline-none focus:ring-0 text-sm !important;
	}

	.text-input {
		@apply px-4 py-3 rounded-md border border-slate-200 focus:border-slate-200 focus:outline-none focus:ring-0 text-sm !important;
	}

	.primary-btn {
		@apply px-4 py-3 bg-arfinnPurple-900 text-center text-white font-semibold text-sm hover:bg-arfinnPurple-800 rounded-md cursor-pointer;
	}

	.message {
		@apply px-4 py-2 bg-white border border-slate-200 rounded-md;
	}

	.message.ai {
		@apply bg-blue-50 border-blue-200 text-right !important;
	}

	.message-text {
		@apply text-sm text-slate-700;
	}
</style>
